<template>
  <div id="app">

    <router-view/>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "app",
  components: {},
  methods: {
    isEmpty(value) {
      return (
        value === undefined ||
        value === null ||
        (typeof value === "object" && Object.keys(value).length === 0) ||
        (typeof value === "string" && value.trim().length===0)
      )
    }
  },
  created() {
    if(localStorage.eleToken){
      let jsondata=sessionStorage.getItem('login');
      let logindata=JSON.parse(jsondata);
      //  存储到vuex中
      this.$store.dispatch('setisAuth',!this.isEmpty(logindata));
      this.$store.dispatch('setuser',logindata);

    }
  }
};
</script>

<style>
html,
body,
#app {
  width: 100%;
  height: 100%;
}
</style>
