<template>
  <div class="fillContainer">
    <div>
      <el-form :inline="true" ref="add_data" :model="search_data">
        <el-form-item>
          <el-date-picker v-model="search_data.startTime" type="datetime" placeholder="选择开始期时间"></el-date-picker>
          --
          <el-date-picker v-model="search_data.endTime" type="datetime" placeholder="选择结束期时间"></el-date-picker>
        </el-form-item>
        <el-form-item >
          <el-button type="primary" size="small" icon="search" @click="handleSearch()">筛选</el-button>
        </el-form-item>
        <el-form-item class="btnRight">
          <el-button v-if="user.identity=== 'manager'" type="primary" size="small" icon="view" @click="handleAdd()">添加</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :data="tableData" border height="400" style="width: 100%;">
        <el-table-column align="center" type="index" label="序号" width="70"></el-table-column>
        <el-table-column align="center" label="创建时间" width="250">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.createdAt}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="type" label="收支类型" width="150"></el-table-column>
        <el-table-column align="center" prop="describe" label="收支描述" width="180"></el-table-column>
        <el-table-column align="center" prop="income" label="收入" width="170">
          <template slot-scope="scope">
            <span style="color:#00d053">{{ scope.row.income}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="expend" label="支出" width="170">
          <template slot-scope="scope">
            <i class="el-icon-s-promotion"></i>
            <span style="color:#f56767">{{ scope.row.expend}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="cash" label="账户现金" width="170">
          <template slot-scope="scope">
            <i class="el-icon-s-promotion"></i>
            <span style="color:#4db3ff">{{ scope.row.cash}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="remark" label="备注" width="220"></el-table-column>
        <el-table-column v-if="user.identity=== 'manager'" label="操作" prop="operation" align="center" width="180">
          <template slot-scope="scope">
            <el-button
              type="warning"
              size="small"
              icon="edit"
              @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button>
            <el-button
              size="small"
              type="danger"
              icon="delete"
              @click="handleDelete(scope.$index, scope.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-row>
        <el-row :span="24">
          <div class="pagination">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="paginations.page_index"
              :page-sizes="paginations.page_sizes"
              :page-size="paginations.page_size"
              :layout="paginations.layout"
              :total="paginations.total"
            ></el-pagination>
          </div>
        </el-row>
      </el-row>
    </div>

    <eldialog :dialog="dialog" :formData="formData" @update="this.getProfile"></eldialog>
  </div>
</template>

<script>
import axios from "axios";
import Dialog from "../components/Dialog";
import { type } from "os";
export default {
  name: "fundlist",
  data() {
    return {
      search_data: {
        startTime: '',
        endTime:''
      },
      filtertableData:[],
      paginations: {
        page_index: 1, //
        total: 0, //总数
        page_size: 5, //一页页显示的条数
        page_sizes: [5, 10, 15, 20], //每页显示多少条
        layout: "total,sizes,prev,pager,next,jumper" //翻页属性
      },
      tableData: [],
      alltableData: [],
      formData: {
        type: "",
        describe: "",
        income: "",
        expend: "",
        cash: "",
        remark: "",
        objectId: ""
      },
      dialog: {
        show: false,
        title: "",
        option: "edit" //判断option的值确定编辑或添加
      }
    };
  },
  computed: {
    user(){
      return this.$store.getters.user;
    }
  },
  components: {
    eldialog: Dialog
  },
  created() {
    this.getProfile();
  },
  methods: {
    handleSearch() {
      //  筛选
      if(!this.search_data.startTime || !this.search_data.endTime){
        this.$message({
          message: '请选择时间区间',
          type: 'warning'
        });
        this.getProfile();
        return;
      }

      //  获取开始和结束的时间
      const sTime = this.search_data.startTime.getTime();
      const eTime = this.search_data.endTime.getTime();
      //  
      this.alltableData = this.filtertableData.filter(item => {
        let date = new Date(item.createdAt);
        let time = date.getTime();
        return time >=sTime && time<=eTime;
      });
      //  分页数据
      this.setPaginations();
    },
    handleSizeChange(page_size) {
      //  切换size
      this.paginations.page_index = 1;
      this.paginations.page_size = page_size;
      //  设置分页数据
      this.tableData = this.alltableData.filter((item, index) => {
        return index < page_size;
      });
    },
    handleCurrentChange(page) {
      //  获取当前页
      let index = this.paginations.page_size * (page - 1);
      //  数据的总数
      let nums = this.paginations.page_size * page;
      //  容器
      let tables = [];
      for (let i = index; i < nums; i++) {
        if (this.alltableData[i]) {
          tables.push(this.alltableData[i]);
        }
        this.tableData = tables;
      }
    },
    getProfile() {
      // 获取表格数据
      axios({
        // url: " /api/1/classes/fundlist",
        url: " /1/classes/fundlist",
        method: "get",
        headers: {
          "X-Bmob-Application-Id": "3486ee36532cacc0aad46564211c4af1",
          "X-Bmob-REST-API-Key": "75f41593b456f26a8fbca37d28032c5e",
          "Content-Type": "application/json"
        }
      }).then(res => {
        console.log(res);
        this.alltableData = res.data.results;
        this.filtertableData = res.data.results;
        //设置分页数据
        this.setPaginations();
        //  console.log(this.tableData);
      });
    },
    setPaginations() {
      //  分页属性设置
      this.paginations.total = this.alltableData.length;
      this.paginations.page_index = 1;
      this.paginations.page_size = 5;
      //  设置默认的分页数据
      this.tableData = this.alltableData.filter((item, index) => {
        return index < this.paginations.page_size;
      });
    },
    handleEdit(index, row) {
      console.log(123);
      //  编辑
      this.dialog = {
        show: true,
        title: "修改资金信息",
        option: "edit"
      };
      this.formData = {
        type: row.type,
        describe: row.describe,
        income: row.income,
        expend: row.expend,
        cash: row.cash,
        remark: row.remark,
        objectId: row.objectId
      };
    },
    handleDelete(index, row) {
      console.log(456);
      axios({
        method: "delete",
        // url: "/api/1/classes/fundlist/" + row.objectId,
        url: "/1/classes/fundlist/" + row.objectId,
        headers: {
          "X-Bmob-Application-Id": "3486ee36532cacc0aad46564211c4af1",
          "X-Bmob-REST-API-Key": "75f41593b456f26a8fbca37d28032c5e",
          "Content-Type": "application/json"
        }
      })
        .then(res => {
          // success
          this.$message({
            message: "删除成功",
            type: "success"
          });
          this.getProfile();
        })
        .catch(err => {
          //  error
          this.$message({
            message: "删除失败",
            type: "error"
          });
        });
    },
    handleAdd() {
      //  添加
      // console.log(789);
      this.dialog = {
        show: true,
        title: "添加资金信息",
        option: "add"
      };
      this.formData = {
        type: "",
        describe: "",
        income: "",
        expend: "",
        cash: "",
        remark: ""
      };
    }
  }
};
</script>


<style scoped>
.fillContainer {
  width: 100%;
  height: 100%;
  padding: 16px;
  box-sizing: border-box;
}
.btnRight {
  float: right;
}
</style>
